import React from 'react';

export default class Main extends React.Component {
    construct(){
        const flowNodes = ("st=>start: \n" +
        "e=>end: , ! Good Luck!\n" +
        "hasFound=>condition: \n" +
        "hasMoney=>condition: \n" +
        "trueMoney=>condition: \n" +
        "hasGanDei=>condition: \n" +
        "trueGanDei=>condition: \n" +
        "enjoyJob=>condition: \n" +
        "wouldRegret=>condition: \n" +
        "later=>condition: \n" +
        "howAbout=>condition: \n" +
        "whyHere=>operation: \n" +
        "askWhyQuit=>operation: \n" +
        "whyQuit=>operation: \n" +
        "continueQuit=>condition: \n" +
        "talkBoss=>operation: \n" +
        "talkBossResult=>condition: \n" +
        "st->hasMoney\n" +
        "hasMoney(yes|)->trueMoney\n" +
        "hasMoney(no|)->hasGanDei\n" +
        "trueMoney(yes)->e\n" +
        "trueMoney(no)->hasFound\n" +
        "hasGanDei(yes)->trueGanDei\n" +
        "hasGanDei(no)->hasFound\n" +
        "trueGanDei(yes)->e\n" +
        "trueGanDei(no)->hasFound\n" +
        "hasFound(yes)->enjoyJob\n" +
        "hasFound(no)->askWhyQuit->whyQuit\n" +
        "enjoyJob(yes)->wouldRegret\n" +
        "enjoyJob(no)->e\n" +
        "wouldRegret(yes)->later\n" +
        "wouldRegret(no)->e\n" +
        "later(yes|)->howAbout\n" +
        "later(no|)->e\n" +
        "howAbout(yes|)->whyHere->whyQuit\n" +
        "howAbout(no|)->continueQuit\n" +
        "whyQuit->talkBoss\n" +
        "talkBoss->talkBossResult\n" +
        "talkBossResult(yes|)->continueQuit\n" +
        "talkBossResult(no|)->hasFound\n" +
        "continueQuit(yes)->wouldRegret\n" +
        "continueQuit(no)->later\n").split("\n"),
            nodeMap = [], flowMap = [];
        flowNodes.forEach(function (node) {
            if (node.indexOf("=>") != -1) {
                //
                var arr = node.split("=>");
                var defs = arr[1].split(":");
                var type = defs[0].trim();
                nodeMap[arr[0].trim()] = {type: type, word: defs[1].trim()};
                if (type == "start")
                    flowMap["start"] = nodeMap[arr[0].trim()];
                else if (type == "end")
                    flowMap["end"] = nodeMap[arr[0].trim()];
            }
        });
        flowNodes.forEach(function (node) {
            if (node.indexOf("->") != -1) {
                //
                var arr = node.split("->");
                arr.forEach(function (flow, index) {
                    var key1 = flow.split("(").shift().trim();
                    var isYes = flow.split("(").pop().indexOf("yes") != -1;
                    var isNo = flow.split("(").pop().indexOf("no") != -1;
                    if (index + 1 < arr.length) {
                        for (var key2 in nodeMap) {
                            if (key1 == key2) {
                                if (nodeMap[key2].type == "condition" && isYes) {
                                    nodeMap[key2].yes = arr[index + 1];
                                    if (flow.indexOf("|") > 0) {
                                        nodeMap[key2].yesWord = flow.split("|").pop().replace(")", "");
                                    }
                                } else if (nodeMap[key2].type == "condition" && isNo) {
                                    nodeMap[key2].no = arr[index + 1];
                                    if (flow.indexOf("|") > 0) {
                                        nodeMap[key2].noWord = flow.split("|").pop().replace(")", "");
                                    }
                                } else {
                                    nodeMap[key2].next = arr[index + 1]
                                }
                            }
                        }
                    }
                });
            }
        });
        this.state = {nodeMap, flowMap};
        this.state.runing = false;
    }

    openModal(){
        this.setState(Object.assign({},this.state,{runing:true}));
    }


    render(){
        return (
            <MainBox onClick={this.openModal}></MainBox>
        )
    }
}

class MainBox extends React.Component {
    render(){
        return (
            <button className="center main_box">
                ?
            </button>
        )
    }
}


